import React, { PureComponent } from 'react';

import classes from './App.css';
import Persons from '../components/Persons/Persons';
import Cockpit from '../components/Cockpit/Cockpit';
import withClass from  '../hoc/withClass';
import Aux from '../hoc/Aux';

export const AuthContext = React.createContext(false);
 
class App extends PureComponent {
  constructor(props) {
    super(props)
    console.log('[App.js] inside constructor()', props)
    // old version state access
    this.state = {
      persons: [
        {id: 0, name: 'max', age: 30},
        {id: 1, name: 'manu', age: 25},
        {id: 2, name: 'imunne', age: 20},
      ],
      otherState: 'some other state',
      showPersons: false,
      toggleClicked: 0,
      authenticated: false,
    }
  }

  componentWillMount() {
    console.log('[App.js] inside componentWillMount()');
  }

  componentDidMount() {
    console.log('[App.js] inside componentDidMount()');
  }  

  // shouldComponentUpdate(nextProps, nextState) {
  //   console.log('[UPDATE App.js] inside shouldComponentUpdate()', nextProps, nextState);
  //   return nextState.persons !== this.state.persons ||
  //   nextState.showPersons !== this.state.showPersons;
  // }

  componentWillUpdate(nextProps, nextState) {
    console.log('[UPDATE App.js] inside componentWillUpdate()', nextProps, nextState);
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    console.log('[UPDATE App.js] inside getDerivedStateFromProps()',
    nextProps,
    prevState
    );

    return prevState;
  }

  getSnapshotBeforeUpdate(prevProps, prevState) {
    console.log('[UPDATE App.js] inside getSnapshotBeforeUpdate()',
    prevProps,
    prevState
    );

    return null;
  }

  componentDidUpdate() {
    console.log('[UPDATE App.js] inside componentDidUpdate()');
  }

  // next gen state access
  // state = {
  //   persons: [
  //     {id: 0, name: 'max', age: 30},
  //     {id: 1, name: 'manu', age: 25},
  //     {id: 2, name: 'imunne', age: 20},
  //   ],
  //   otherState: 'some other state',
  //   showPersons: false,
  // }

  nameChangeHandler = (event, id) => {
    const personIndex = this.state.persons.findIndex(p => {
      return p.id === id
    })

    const person = {
      ...this.state.persons[personIndex]
    }

    person.name = event.target.value
    const persons = [...this.state.persons]
    persons[personIndex] = person

    this.setState({persons: persons})
  }

  deletePersonHandler = (personIndex) => {
    const persons = [...this.state.persons]
    persons.splice(personIndex, 1)
    this.setState({persons: persons})
  }

  togglePersonsHandler = () => {
    const doesShow = this.state.showPersons
    this.setState( (prevState, props) => {
      return {
        showPersons: !doesShow,
        toggleClicked: prevState.toggleClicked + 1,
      }      
    })
  }

  loginHandler = () => {
    this.setState((prevState) => {
      return {authenticated: !prevState.authenticated} 
    })     
  }

  disableHandler = () => { 
    const flag = !this.state.authenticated;
    return flag
  }


  render() { 
    console.log('[App.js] inside render()');
    let persons = null;    

    if(this.state.showPersons) {
      persons = <Persons 
            persons={this.state.persons}
            delete={this.deletePersonHandler}
            changed={this.nameChangeHandler}
            disable={this.disableHandler}
            />;
    }

    return (      
      <Aux>
        <button className={classes.Button} onClick={() => {this.setState({showPersons: true})}}>Show Persons</button>
        <Cockpit
          appTitle={this.props.appTitle} 
          clicked={this.togglePersonsHandler}
          showPersons={this.state.showPersons}
          persons={this.state.persons}
          login={this.loginHandler}
        />
        <AuthContext.Provider value={this.state.authenticated}>
          {persons}
        </AuthContext.Provider>        
      </Aux>      
    );
  }
}

export default withClass(App, classes.App);



