import React, { Component } from 'react';
import PropTypes from 'prop-types';

import classes from './Person.css';
import withClass from '../../../hoc/withClass';
import Aux from '../../../hoc/Aux';
import { AuthContext } from '../../../containers/App';

class Person extends Component {
    constructor(props) {
        super(props)
        console.log('[Person.js] inside constructor()', props)
        this.inputElement = React.createRef();
    }

    componentWillMount() {
        console.log('[Person.js] inside componentWillMount()');
    }

    componentDidMount() {
        console.log('[Person.js] inside componentDidMount()');
        if (this.props.position === 0) {
            this.inputElement.current.focus();
        }
    }

    focus() {
        this.inputElement.current.focus();
    }

    render() {
        console.log('[Person.js] inside render()')
        return (
            <Aux>
                <AuthContext.Consumer>
                    {auth => auth ? <p>Authenticated!</p> : null}
                </AuthContext.Consumer>
                <p className='Par' >I`m {this.props.name}  and I am {this.props.age} years old</p>
                {this.props.disable ? 
                <button className={classes.Button} onClick={this.props.click} >Delete Person</button> :
                <button className={classes.Button} onClick={this.props.click} disabled>Delete Person</button>
                }
                <p>{this.props.children}</p>
                <input
                    ref={this.inputElement}
                    type='text'
                    value={this.props.name}
                    onChange={this.props.changed}
                />
            </Aux>
        )
    }
}

Person.propTypes = {
    changed: PropTypes.func,
    click: PropTypes.func,
    name: PropTypes.string,
    age: PropTypes.number,
}

export default withClass(Person, classes.Person);


